<template>
  <div>
    <Head activeIndex="1" :showHome='showHome'></Head>
    <div>
      <p class="slideShine header-text"></p>
      <p class="masked header-text-bottom"> </p>
      <img src="../../assets/img/pg4-copy.jpg" style="width: 100%;"/>
    </div>
    <div class="article">
      <p class="head"></p>
      <p class="describe"></p>
      <div class="clearfix box">
        <div class="inline-item first cursor" v-for="(item, index) in articles" @click="toOneArticle(item.article_id)">
          <img :src="item.picture_addr" class="article-img">
          <h3 style="">{{item.title}}</h3>
          <div class="w100">
            <p class="article-desc">{{item.description.substring(0, 75) + '...'}}</p>
          </div>
        </div>
      </div>
      <div class="mt20">
        <p class="more more-style cursor" @click="toMorePage('article')">&nbsp;>> </p>
      </div>
    </div>

    <div class="music">
      <p class="head"></p>
      <p class="describe"></p>
      <section class="recent-works">
        <div style="margin: 10px auto">
          <div v-for="(item, index) in musics" style="width: 30%;display: inline-block">
            <div class="recent-work-wrap">
              <div>
                <img :src="musicImg[index]">
                <div class="overlay"  @click="toOneMusic(item.music_id)">
                  <div class="recent-work-inner">
                    <h3>{{item.name}}</h3>
                    <p>{{item.description}}</p>
                    <span class="symbol"></span>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="mt20">
            <p class="more more-style cursor" @click="toMorePage('music')">&nbsp;>> </p>
          </div>
        </div>
      </section>
    </div>


    <div class="test">
      <p class="head"></p>
      <p class="describe"></p>
      <div class="clearfix box">
        <div class="inline-item-low cursor" v-for="item in testPages" @click="toOneTest(item.test_id)">
          <img :src="item.picture_addr" style="width: 80%; height: 250px; background: #FFF;margin-top: 25px;">
          <p class="test-item-name">{{item.test_name}}</p>
        </div>
      </div>
      <div class="mt10 ml10">
        <p class="more more-style cursor" @click="toMorePage('test')">&nbsp;>> </p>
      </div>
    </div>

    <div class="forum mt30">
      <section class="partner">
        <div>
          <h2 class="head pt40"></h2>
          <p class="describe">.</p>
          <div class="enter mt15">
            <p class="enter-forum cursor" @click="toMorePage('forum')"></p>
          </div>
        </div>
      </section>
    </div>

    <foot></foot>
  </div>
</template>


<script>
  import Head from '../common/Head'
  import Foot from '../common/Foot'

  export default {
    data() {
      return {
        activeIndex: '1',
        articles: [],
        musics: [],
        testPages: [],
        showHome: false,
        musicImg: ['/static/img/music/mus1.jpg', '/static/img/music/mus2.jpg', '/static/img/music/mus3.jpg',
          '/static/img/music/mus4.jpg', '/static/img/music/mus5.jpg', '/static/img/music/mus6.jpg']
      };
    },
    mounted() {
      let vm = this;

      vm.getArticles();
      vm.getMusics();
      vm.getTestPage();

      if(window.sessionStorage.getItem('usr')) {
        vm.showHome = true;
      }else {
        vm.showHome = false;
      }
    },
    methods: {
      // 
      getArticles() {
        let vm = this;

        vm.$ajax.post('/apis/article/getArticle', {num: 3}).then(function (res) {
          if(res.data.state) {
            vm.articles = res.data.articles;
          }else {
            //
          }
        });
      },
      getMusics() {
        let vm = this;

        vm.$ajax.post('/apis/music/getMusic', {num: 6}).then(function (res) {
          if(res.data.state) {
            vm.musics = res.data.musics;
          }else {
            //
          }
        });
      },
      getTestPage() {
        let vm = this;

        vm.$ajax.post('/apis/test/getTestPage', {num: 3}).then(function (res) {
          if (res.data.state) {
            vm.testPages = res.data.testPages;
          } else {
            //
          }
        });
      },
      toOneArticle(num) {
        this.$router.push({
          name: 'readArticle',
          params: {
            articleId: num
          }
        })
      },
      toOneMusic(num) {
        /*window.open('http://localhost:8080/#/play/' + num, '_blank');*/
        window.open('http://localhost:8888/#/play/' + num, '_blank');
      },
      toOneTest(num) {
        this.$router.push({
          name: 'testPaper',
          params: {
            paperId: num
          }
        })
      },
      toMorePage(router) {
        this.$router.push({
          name: router,
        });
      }
    },
    components: {
      Head,
      Foot
    }
  }

</script>

<style scoped>
 @import "style.css";
</style>






